import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import ErrorView from '../views/ErrorView.vue'
import HomeOg from '../views/HomeOg.vue'
import DireState from '../views/DireState.vue'
import SustValu from '../views/SustValu.vue'
import AboutView from '../views/AboutView.vue'
import BusiMode from '../views/BusiMode.vue'
import BusiRevi from '../views/BusiRevi.vue'
import OurSub from '../views/OurSub.vue'
import RiskMa from '../views/RiskMa.vue'
import FinSt from '../views/FinSt.vue'

const routes = [
  {
    path: '/',
    name: 'homeog',
    component: HomeOg
  },
  {
    path: '/subsi',
    name: 'subsi',
    component: OurSub
  },
  {
    path: '/financial',
    name: 'financial',
    component: FinSt
  },
  {
    path: '/risk',
    name: 'risk',
    component: RiskMa
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/review',
    name: 'review',
    component: BusiRevi
  },
  
  {
    path: '/business',
    name: 'business',
    component: BusiMode
  },
  {
    path: '/dire',
    name: 'dire',
    component: DireState
  },
  {
    path: '/sust',
    name: 'sust',
    component: SustValu
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
 
 
  {
    path: '/:catchAll(.*)',
    name: 'error',
    component: ErrorView
  },
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
